/* Navbars.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 10px 20px;
    color: white;
  }
  
  .logo {
    font-size: 24px;
    font-weight: bold;
  }
  
  .search-container {
    display: flex;
    align-items: center;
  }
  
  .search-input {
    padding: 5px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    outline: none;
  }
  
  .search-button {
    padding: 6px 12px;
    margin-left: 10px;
    font-size: 16px;
    border: none;
    background-color: #555;
    color: white;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .search-button:hover {
    background-color: #777;
  }
  
  .category-dropdown {
    margin-left: 20px;
  }
  
  .category-dropdown select {
    padding: 5px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
  }
  