.movie-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.movie-card {
  position: relative;
  margin: 15px;
  width: 200px;
  cursor: pointer;
  text-align: center;
}

.movie-card img {
  width: 100%;
  height: auto;
  transition: transform 0.3s ease;
}

.movie-card:hover img {
  transform: scale(1.05);
}
